@font-face {
    font-family: "Nexa";
    font-weight: 400;
    src:
      local("NexaRegular"),
      local("Nexa Regular"),
      url(/static/media/NexaRegular.b36eb4de.woff) format("woff"),
      url(/static/media/NexaRegular.5946dbf9.woff2) format("woff2"),
      url(/static/media/NexaRegular.085aae67.ttf) format("ttf");
    font-display: swap;
  }
  
 @font-face {
    font-family: "Nexa";
    font-weight: 700;
    src:
      local("Nexa Bold"),
      local("Nexa XBold"),
      local("NexaBold"),
      url(/static/media/NexaBold.4f797d1c.woff) format("woff"),
      url(/static/media/NexaBold.d7cd522c.woff2) format("woff2"),
      url(/static/media/NexaBold.b528f262.ttf) format("ttf");
    font-display: swap;
  }
  
